import React, { Component } from "react";
import Web3 from "web3";
import ERC20 from "../abis/ERC20.json";
import Staker from "../abis/Staker.json";
import Navbar from "./Navbar";
import Main from "./Main";
import "./App.css";
import eth from "../eth-logo.png";

class App extends Component {
  async componentWillMount() {
    await this.loadWeb3();
    await this.loadBlockchainData();
  }

  async updateBalance() {
    await this.loadWeb3();
    await this.loadBlockchainData();
    const web3 = window.web3;
    const erc20 = new web3.eth.Contract(ERC20.abi, this.state.tokenAddress);
    await this.setState({ erc20 });
    let ethBalance = await web3.eth.getBalance(this.state.account);
    await this.setState({ ethBalance: ethBalance.toString() });
    await this.updateStakingBalance();
    await this.updateContractLaunchTime();
    await this.updateUserExitValue();
  }

  async updateUserExitValue() {
    const web3 = window.web3;
    const networkId = await web3.eth.net.getId();
    const stakerData = Staker.networks[networkId];
    const staker = new web3.eth.Contract(
        Staker.abi,
        stakerData.address
    );
    let currentExitValue = await staker.methods
        .getCurrentUserExitValue(this.state.account)
        .call();
    this.setState({currentExitValue: currentExitValue.toString()});
    this.setState({contractAddress: "http://etherscan.io/address/".concat(stakerData.address)})
  }

  async updateStakingBalance() {
    const web3 = window.web3;
    const networkId = await web3.eth.net.getId();
    const stakerData = Staker.networks[networkId];
    const staker = new web3.eth.Contract(
        Staker.abi,
        stakerData.address
    );
    let stakingBalance = await staker.methods
        .getUserEthStaked(this.state.account)
        .call();
    this.setState({stakingBalance: stakingBalance.toString()});

    let currentPotential = (await staker.methods
        .getCurrentPotential()
        .call()).toString();

    if (currentPotential.localeCompare("0") === 0) {
      currentPotential = "\u221E";
    } else {
      currentPotential = window.web3.utils.fromWei(currentPotential,"Ether").split(".")[0]
    }

    this.setState({currentPotential: currentPotential});

    let totalETH = await staker.methods
        .totalETH()
        .call();
    this.setState({totalETH: totalETH.toString()});
  }

  async updateContractLaunchTime() {
    const web3 = window.web3;
    const networkId = await web3.eth.net.getId();
    const stakerData = Staker.networks[networkId];
    const staker = new web3.eth.Contract(
      Staker.abi,
      stakerData.address
    );

    let currentTime = new Date();
    const contractLaunchTime = new Date(await staker.methods
        .contractLaunchTime()
        .call() * 1000);

    const earliestUnstakeDate = new Date(contractLaunchTime.getTime()+1000*60*60*24);

    this.setState({ contractLaunchTime: contractLaunchTime});
    this.setState({ fastestUnlockTime: earliestUnstakeDate});

    let userUnstakeTime = await staker.methods
      .getUserUnlockTime(this.state.account)
      .call();
    this.setState({ userUnstakeTime: new Date(userUnstakeTime * 1000) });

    this.setState({ contractStarted: currentTime > this.state.contractLaunchTime});
    this.setState({ userCanUnstake: currentTime > this.state.userUnstakeTime});

    let owner = await staker.methods
        .owner()
        .call();
    this.setState({ isOwner: this.state.account.localeCompare(owner)});
  }

  async loadBlockchainData() {
    const web3 = window.web3;

    const accounts = await web3.eth.getAccounts();
    this.setState({ account: accounts[0] });
    this.setState({
      tokenAddress: "0xC02aaA39b223FE8D0A0e5C4F27eAD9083C756Cc2",
    });
    this.setState({ image: eth });
    this.setState({ tokenName: "ETH" });

    const networkId = await web3.eth.net.getId();

    const erc20 = new web3.eth.Contract(ERC20.abi, this.state.tokenAddress);
    this.setState({ erc20 });
    let ethBalance = await web3.eth.getBalance(this.state.account);
    this.setState({ ethBalance: ethBalance.toString() });

    // Load Staker
    const stakerData = Staker.networks[networkId];
    if (stakerData) {
      const staker = new web3.eth.Contract(
        Staker.abi,
        stakerData.address
      );
      this.setState({ staker });
      this.updateStakingBalance();
    } else {
      window.alert("Staker contract not deployed to detected network.");
    }

    this.setState({ loading: false });
  }

  async loadWeb3() {
    if (window.ethereum) {
      window.web3 = new Web3(window.ethereum);
      await window.ethereum.enable();
    } else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider);
    } else {
      window.alert(
        "Non-Ethereum browser detected. You should consider trying MetaMask!"
      );
    }
  }

  componentDidMount() {
    this.updateBalance();
    setInterval(this.updateBalance.bind(this), 30000);
  }

  stakeTokens = (amount) => {
    this.setState({ loading: true });
    this.state.staker.methods
        .stake(this.state.referral.localeCompare(this.state.account) ? this.state.referral : '0x0000000000000000000000000000000000000000')
        .send({ from: this.state.account, value: amount })
        .on("transactionHash", (hash)=> {
          this.setState({ loading: false });
        });
  };

  unstakeTokens = () => {
    this.setState({ loading: true });
    this.state.staker.methods
      .unstakeTokens()
      .send({ from: this.state.account })
      .on("transactionHash", (hash) => {
        this.setState({ loading: false });
      });
  };

  constructor(props) {
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);

    super(props);
    this.state = {
      account: "0x0",
      staker: {},
      ethBalance: "0",
      currentExitValue: "0",
      stakingBalance: "0",
      loading: true,
      image: eth,
      tokenName: "ETH",
      contractLaunchTime: new Date(),
      fastestUnlockTime: new Date(),
      userUnstakeTime: new Date(),
      contractStarted: true,
      userCanUnstake: true,
      currentPotential: "0",
      totalETH: "0",
      contractAddress: "0",
      referral: urlParams.get('r')  == null ? '0x0000000000000000000000000000000000000000' : urlParams.get('r'),
    };
  }

  render() {
    let content;
    if (this.state.loading) {
      content = (
        <p id="loader" className="text-center">
          Loading...
        </p>
      );
    } else {
      content = (
          <Main
              ethBalance={this.state.ethBalance}
              currentExitValue={this.state.currentExitValue}
              stakingBalance={this.state.stakingBalance}
              stakeTokens={this.stakeTokens.bind(this)}
              unstakeTokens={this.unstakeTokens.bind(this)}
              tokenName={this.state.tokenName}
              image={this.state.image}
              updateBalance={this.updateBalance.bind(this)}
              contractLaunchTime={this.state.contractLaunchTime}
              fastestUnlockTime={this.state.fastestUnlockTime}
              userUnstakeTime={this.state.userUnstakeTime}
              contractStarted={this.state.contractStarted}
              userCanUnstake={this.state.userCanUnstake}
              isOwner={this.state.isOwner}
              currentPotential={this.state.currentPotential}
              totalETH={this.state.totalETH}
              contractAddress={this.state.contractAddress}
              account={this.state.account}
          />
      );
    }

    return (
      <div>
        <Navbar account={this.state.account} />
        <div className="container-fluid mt-5">
          <div className="row">
            <main
              role="main"
              className="col-lg-12 ml-auto mr-auto"
              style={{ maxWidth: "600px" }}
            >
              <div className="content mr-auto ml-auto">
                {content}
              </div>
            </main>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
